
"use client";

import { useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { format } from 'date-fns';
import { CalendarIcon, Loader2, Upload, Wand2, X } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { Badge } from '@/components/ui/badge';
import { cn } from '@/lib/utils';
import { productTypes } from '@/lib/data';
import { getIcingSuggestions } from '../actions';

const formSchema = z.object({
  name: z.string().min(2, { message: 'Name must be at least 2 characters.' }),
  email: z.string().email({ message: 'Please enter a valid email address.' }),
  productType: z.string({ required_error: 'Please select a product type.' }),
  designTheme: z.string().min(10, { message: 'Please describe your design in at least 10 characters.' }),
  designImage: z.any().optional(),
  flavor: z.string().min(3, { message: 'Flavor must be at least 3 characters.' }),
  icing: z.string().min(3, { message: 'Icing flavor must be at least 3 characters.' }),
  eventDate: z.date({ required_error: 'A date for your event is required.' }),
  servings: z.coerce.number().min(1, { message: 'Please enter a valid number of servings.' }),
});

export default function CustomOrderForm() {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSuggesting, setIsSuggesting] = useState(false);
  const [suggestions, setSuggestions] = useState<string[]>([]);
  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: '',
      email: '',
      designTheme: '',
      flavor: '',
      icing: '',
    },
  });

  const handleSuggestIcing = async () => {
    const productType = form.getValues('productType');
    const desiredFlavor = form.getValues('flavor');

    if (!productType || !desiredFlavor) {
      toast({
        variant: 'destructive',
        title: 'Missing Information',
        description: 'Please select a product type and enter a desired flavor first.',
      });
      return;
    }

    setIsSuggesting(true);
    setSuggestions([]);
    const result = await getIcingSuggestions({ productType, desiredFlavor });
    setIsSuggesting(false);

    if (result.error) {
      toast({
        variant: 'destructive',
        title: 'Suggestion Failed',
        description: result.error,
      });
    } else if (result.suggestions) {
      setSuggestions(result.suggestions);
    }
  };

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsSubmitting(true);
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 2000));

    setIsSubmitting(false);

    toast({
      title: 'Order Request Sent!',
      description: `Thank you, ${values.name}. We've received your request and will be in touch shortly.`,
    });

    form.reset();
    setSuggestions([]);
  }

  const selectedFile = form.watch('designImage');

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Full Name</FormLabel>
                <FormControl>
                  <Input placeholder="Jane Doe" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="email"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Email Address</FormLabel>
                <FormControl>
                  <Input placeholder="jane.doe@example.com" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <FormField
          control={form.control}
          name="productType"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Product Type</FormLabel>
              <Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl>
                  <SelectTrigger>
                    <SelectValue placeholder="Select a product" />
                  </SelectTrigger>
                </FormControl>
                <SelectContent>
                  {productTypes.map((type) => (
                    <SelectItem key={type} value={type}>{type}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <FormMessage />
            </FormItem>
          )}
        />
        
        <FormField
          control={form.control}
          name="designTheme"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Design / Theme</FormLabel>
              <FormControl>
                <Textarea placeholder="e.g., enchanted forest theme, minimalist with gold leaf, superhero design..." {...field} />
              </FormControl>
              <FormDescription>
                Describe the look, feel, or any specific characters/elements you'd like.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="designImage"
          render={({ field: { onChange, onBlur, name, ref } }) => (
            <FormItem>
              <FormLabel>Upload Design</FormLabel>
              <FormControl>
                <Input 
                    type="file" 
                    accept="image/*" 
                    ref={fileInputRef}
                    className="hidden"
                    onChange={(e) => onChange(e.target.files)}
                    onBlur={onBlur}
                    name={name}
                />
              </FormControl>
              <div className='flex items-center gap-4'>
                <Button 
                    type='button' 
                    variant='outline'
                    onClick={() => fileInputRef.current?.click()}
                >
                    <Upload className="mr-2 h-4 w-4" />
                    Choose File
                </Button>
                {selectedFile?.[0] && (
                    <div className='flex items-center gap-2 text-sm text-muted-foreground'>
                        <span>{selectedFile[0].name}</span>
                        <Button 
                            type='button' 
                            variant='ghost'
                            size='icon'
                            className='h-6 w-6'
                            onClick={() => {
                                form.setValue('designImage', null, { shouldValidate: true });
                                if(fileInputRef.current) {
                                    fileInputRef.current.value = '';
                                }
                            }}
                        >
                            <X className='h-4 w-4' />
                        </Button>
                    </div>
                )}
              </div>
              <FormDescription>
                Have a specific design in mind? Upload a picture for us.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <FormField
              control={form.control}
              name="flavor"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Cake/Base Flavor</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Chocolate, Vanilla, Strawberry" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="icing"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Icing Flavor</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Cream Cheese, Buttercream" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
        </div>

        <div className='space-y-4 rounded-lg border bg-accent/20 p-4'>
            <div className='flex items-center gap-2'>
                <Wand2 className='h-5 w-5 text-accent-foreground' />
                <h3 className='font-semibold text-accent-foreground'>Need inspiration?</h3>
            </div>
            <p className='text-sm text-accent-foreground/80'>Let our AI chef suggest icing pairings based on your flavor!</p>
            <Button
              type='button'
              variant='secondary'
              onClick={handleSuggestIcing}
              disabled={isSuggesting}
            >
              {isSuggesting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Suggesting...
                </>
              ) : (
                'Suggest Icing Flavors'
              )}
            </Button>
            {suggestions.length > 0 && (
                <div className='pt-2'>
                    <p className='text-sm font-medium mb-2 text-accent-foreground'>Suggestions:</p>
                    <div className='flex flex-wrap gap-2'>
                        {suggestions.map((suggestion, index) => (
                            <Badge 
                                key={index} 
                                variant='outline' 
                                className='cursor-pointer bg-background text-foreground'
                                onClick={() => form.setValue('icing', suggestion)}
                            >
                                {suggestion}
                            </Badge>
                        ))}
                    </div>
                </div>
            )}
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <FormField
              control={form.control}
              name="eventDate"
              render={({ field }) => (
                <FormItem className="flex flex-col">
                  <FormLabel>Pickup / Event Date</FormLabel>
                  <Popover>
                    <PopoverTrigger asChild>
                      <FormControl>
                        <Button
                          variant={"outline"}
                          className={cn(
                            "w-full pl-3 text-left font-normal",
                            !field.value && "text-muted-foreground"
                          )}
                        >
                          {field.value ? (
                            format(field.value, "PPP")
                          ) : (
                            <span>Pick a date</span>
                          )}
                          <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                        </Button>
                      </FormControl>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <Calendar
                        mode="single"
                        selected={field.value}
                        onSelect={field.onChange}
                        disabled={(date) =>
                          date < new Date(new Date().setHours(0,0,0,0))
                        }
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="servings"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Number of Servings</FormLabel>
                  <FormControl>
                    <Input type="number" placeholder="e.g., 20" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
        </div>

        <div className="flex justify-end">
            <Button type="submit" size="lg" disabled={isSubmitting}>
              {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Submit Request
            </Button>
        </div>
      </form>
    </Form>
  );
}
